import { message } from "antd";

export const success = messager => {
	message.success(messager ? messager : "Add to cart success");
};
export const warning = messager => {
	message.warning(messager ? messager : "Warning");
};

export const error = messager => {
	message.error(messager ? messager : "Error");
};



